<template>
	<div class="example">
		<div class="ep-card mb" :style="{ backgroundImage: 'url('+ carousel +')' }" >
			<div class="ep">
				<div class="title">{{exampleData.title}}</div>
				<p class="description"><img :src="comment"> {{exampleData.description}}</p>

				<Setmeal :data="exampleData.setmeal" />
			</div>
		</div>

		<div class="ep-content mb">
			<ExampleDetail :data="exampleData" />
		</div>

		<div class="tp-artical">
			<div class="header-name">
				<div class="zh">其他套餐</div>
			</div>

			<Recommend :data="recommend" @changeReItem="changeReItem" />
		</div>
	</div>
</template>

<script>
	import ExampleDetail from '@/components/ExampleDetail.vue'
	import Setmeal from '@/components/Setmeal.vue'
	import Recommend from '@/components/Recommend.vue'

	export default {
		name: 'example',
		components: { ExampleDetail, Setmeal, Recommend },
		data() {
			return {
				exampleData: [],
				recommend: [],
				carousel: `${process.env.BASE_URL}images/example/banner.jpg`,
				comment: `${process.env.BASE_URL}images/example/comment.png`,
				publicPath: process.env.BASE_URL,
			}
		},
		created() {
			fnExmaple(this, { id : this.$route.query.id || 1 });
			fnRecommend(this);
			this.$emit('handlerTabbar', true);
		},
		methods: {
			changeReItem(item) {
				this.$emit('handlerTabbar', false);
				sessionStorage.detail = JSON.stringify(item);
				this.$router.push({ path: 'redetail', query: { id: item.id }});
			}
		}
	}

	function fnExmaple(that, params) {
		that.$request['example' + params.id](params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.exampleData = result;
			}
		})
	}

	function fnRecommend(that, params) {
		that.$request.recommend(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.recommend = result.list;
			}
		})
	}
</script>

<style lang="scss">
	@import './../styles/example.scss'
</style>